class RainDrop {
    constructor(x, y, canvas, ctx, dom) {
        this.weight = 0;
        this.ctx = ctx;
        this.canvas = canvas;
        this.DOM = dom;
        this.x = x;
        this.y = 0 - Math.random()*this.canvas.height;
    }
    draw() {
        this.ctx.fillStyle = "#d4f1f9";
        this.ctx.beginPath();
        this.ctx.moveTo(this.x, this.y);
        this.ctx.lineTo(this.x + 5, this.y - 10);
        this.ctx.lineTo(this.x + 10, this.y)
        this.ctx.arc(this.x + 5, this.y, 5, 0, Math.PI);

        this.ctx.closePath();
        this.ctx.fill();
    }
    update() {
        this.weight += 0.05;

        if (this.y > this.canvas.height) {
            this.y = 0 - Math.random() * this.canvas.height;
            this.x = 0 + Math.random() * this.canvas.width;
            this.weight = 0;
        }
        if (
            this.x+5 > this.DOM.x  &&
            this.x < this.DOM.x + this.DOM.width &&
            this.y+10 > this.DOM.y
        ) {
            this.x > (this.DOM.x + this.DOM.width/2) ? this.x += 3 : this.x -=3;
        } else {
            this.y += this.weight;
        }
    }
}

function init() {
    for (let index = 0; index < particlesLength; index++) {
        particlesArray.push(new RainDrop(Math.random() * canvas.width,0, canvas, ctx, dom));
    }
    animation();
}


function animation() {
    ctx.fillStyle = 'rgba(255,255,255,0.1)';
    ctx.fillRect(0, 0, canvas.width, canvas.height)
    particlesArray.forEach(d => {
        d.draw();
        d.update();

    })
    requestAnimationFrame(animation);
}

var canvas = null;
var dom = null;
var ctx = null;
var particlesLength = 200;
var particlesArray = [];
self.onmessage = function (message) {
    canvas = message.data.canvas;
    ctx = message.data.canvas.getContext('2d');
    dom = message.data.domObj;
    init();
}